const googleIconUri =
  "data:image/svg+xml;base64,";
const facebookIconUri =
  "data:image/svg+xml;base64,";
const appleIconUri =
  "data:image/svg+xml;base64,";
const discordIconUri =
  "data:image/svg+xml;base64,";
const coinbaseIconUri =
  "data:image/svg+xml;base64,";
const lineIconUri =
  "data:image/svg+xml;base64,";
const farcasterIconUri =
  "data:image/svg+xml;base64,";
const telegramIconUri =
  "data:image/svg+xml;base64,";
const twitchIconUri =
  "data:image/svg+xml;base64,";
const githubIconUri =
  "data:image/svg+xml;base64,";
const xIcon =
  "data:image/svg+xml;base64,";
const emailIcon =
  "data:image/svg+xml;base64,";
const phoneIcon =
  "data:image/svg+xml;base64,";
export const genericTokenIcon =
  "data:image/svg+xml;base64,";
const guestIcon =
  "data:image/svg+xml;base64,";
export const genericWalletIcon =
  "data:image/svg+xml;base64,";
const passkeyIcon =
  "data:image/svg+xml;base64,";

export const socialIcons = {
  google: googleIconUri,
  apple: appleIconUri,
  coinbase: coinbaseIconUri,
  facebook: facebookIconUri,
  discord: discordIconUri,
  line: lineIconUri,
  x: xIcon,
  farcaster: farcasterIconUri,
  telegram: telegramIconUri,
  twitch: twitchIconUri,
  github: githubIconUri,
};

/**
 * Gets the icon URI for a given social authentication provider
 * @param provider - The authentication provider to get the icon for
 * @returns A data URI containing the SVG icon for the provider, or a generic wallet icon if the provider is not recognized
 * @example
 * ```tsx
 * const googleIcon = getSocialIcon("google")
 * const appleIcon = getSocialIcon("apple")
 * ```
 */
export function getSocialIcon(provider: AuthOption | ({} & string)): string {
  switch (provider) {
    case "google":
      return googleIconUri;
    case "coinbase":
      return coinbaseIconUri;
    case "apple":
      return appleIconUri;
    case "facebook":
      return facebookIconUri;
    case "phone":
      return phoneIcon;
    case "email":
      return emailIcon;
    case "passkey":
      return passkeyIcon;
    case "discord":
      return discordIconUri;
    case "line":
      return lineIconUri;
    case "x":
      return xIcon;
    case "farcaster":
      return farcasterIconUri;
    case "telegram":
      return telegramIconUri;
    case "twitch":
      return twitchIconUri;
    case "github":
      return githubIconUri;
    case "guest":
      return guestIcon;
    default:
      return genericWalletIcon;
  }
}
